/* alumni-sans-600 - cyrillic */
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/alumni-sans-v18-cyrillic-600.woff2") format("woff2");
}

